import { mapState,mapGetters } from 'vuex';
export default {
	data() {
		return {
			list:[]
		}
	},
	
	components:{
	},
	mounted() {
		this.get_todo_list()
	},
	computed: {
		...mapState(['bgt_c_task','bgt_ct_id']),
		...mapGetters(['user_role','id']),
	},
	methods: {
		get_todo_list(){
			this.$http.get('personwx.headtodolist/1.0/',{
				proId:this.bgt_ct_id,
				pid:this.id
			}).then(res =>{
				console.log(res)
				this.list = this.$utils._get(res,'data.data',[])
			})
		}
	}
}
